.header {
  width: 100%;
  padding: 10px 0;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  margin: 0 auto;
  box-shadow: 0 2px 4px rgb(0 0 0 / 8%);
  background-color: white;
}

.wrapper {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.logo {
  width: 70px;
}

.logo > img {
  width: 100%;
}

.link {
  position: relative;
  font-weight: 600;
  font-size: 24px;
  display: flex;
  align-items: center;
  transition: border-color 0.3s, color 0.3s;
  background-color: transparent;
}

.link:hover {
  color: #e43332;
}

.link:hover > .cartQuantity {
  background-color: #212121;
}

.cart > svg {
  font-size: 33px;
}

.active {
  border-bottom: 2px solid;
  border-color: #212121;
}

.active:hover {
  border-color: #e43332;
}

.navigation {
  display: flex;
  align-items: center;
}

.navigation > div, 
.navigation > a {
  margin-left: 15px;
}

.cartQuantity {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 24px;
  height: 24px;
  border-radius: 50%;
  bottom: -7px;
  right: -7px;
  font-size: 12px;
  color: white;
  background-color: #e43332;
  border: 2px solid white;
  transition: 0.3s;
}

.logout {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.logout > svg {
  font-size: 24px;
}

.logout > span {
  font-weight: 400;
  font-size: 15px;
}

@media screen and (max-width: 480px) { 
  .logo {
    width: 50px;
  }

  .link {
    font-size: 20px;
  }

  .logout > span {
    display: none;
  }

  .logout > svg {
    font-size: 26px;
  }

  .cart > svg {
    font-size: 28px;
  }

  .cartQuantity {
    min-width: 20px;
    height: 20px;
    bottom: -7px;
    right: -7px;
    font-size: 12px;
    border: 2px solid white;
  }

  .navigation > div, 
.navigation > a {
  margin-left: 10px;
}
}